const DEBUG = false
// let BASE_URL = DEBUG ? 'http://223.203.218.93' : '';//
//let BASE_URL = DEBUG ? 'http://10.100.157.19:8080' : '';
// let BASE_URL = DEBUG ? 'http://172.17.199.109:8080' : '';//
//let BASE_URL = DEBUG ? 'http://10.100.157.34:8080' : '';//
//let LOGIN_URL = DEBUG ? 'http://10.100.156.81:8080' : '';
let BASE_URL = DEBUG ? 'http://172.17.200.61:8400' : '';
let token = '';
// debugger
if(location.hash.split('?')[1]){
   token = location.hash.split('?')[1];
   if(token=='hidden=hidden'){
    token=''
   }else{
    token = '?token='+location.hash.split('?')[1];
   }
}else{
        token = ''
}
const API = {
	// DT
    'temmodel':{
        'temList':BASE_URL + '/dt/front/tpl/list.ft' + token,//
        'temlistdec':BASE_URL + '/dt/front/tpl/detail.ft' + token,//
        'addteminfor':BASE_URL + '/dt/front/tpl/add.ft' + token,//
        'deletetem':BASE_URL + '/dt/front/tpl/delete.ft' + token,//
        'updatetem':BASE_URL + '/dt/front/tpl/update.ft' + token,//
        'isRename':BASE_URL + '/dt/front/check/template.ft' + token,//

    },
    'RealColumn':{
    	'colList':BASE_URL + '/dt/front/instance/list.ft' + token,//
    	'addexample':BASE_URL + '/dt/front/instance/add.ft' + token,//
    	'deleteExp':BASE_URL + '/dt/front/instance/delete.ft' + token,//
    	'exampledec':BASE_URL + '/dt/front/instance/detail.ft' + token,//
        'updatedatil':BASE_URL + '/dt/front/instance/update.ft' + token,//
        'updateexa':BASE_URL + '/dt/front/instance/update/expectvalue.ft' + token,//
        'charts':BASE_URL + '/dt/front/instance/tsdb.ft' + token,//
        'isRename':BASE_URL + '/dt/front/check/instance.ft' + token,//
    }
}
export default API;



